<template>
  <div class="address">
    <van-nav-bar title="地址管理" left-text="返回" left-arrow @click-left="onClickLeft" />

    <div v-if="list.length == 0" class="tig">还么有地址，快去添加吧！</div>

    <van-address-list
      v-model="chosenAddressId"
      :list="list"
      default-tag-text="默认"
      @add="onAdd"
      @edit="onEdit"
    ></van-address-list>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import Util from "../util/utils";
import Api from "../api/api";

export default {
  data() {
    return {
      //现在选择的地址id
      chosenAddressId: "0"
    };
  },
  created() {
    // 第一步从后台获取当前用户的地址数据
    let tokenString = Util.getCookies(this);

    Util.showLoading(this);

    Api.findAddress({ tokenString }).then(res => {
      if (res.data.code == 20000) {
        this.$toast(res.data.msg);

        // 把查询到的信息处理并初始化
        let data = res.data.result;

        data.forEach(v => {
          v.address = v.province + v.city + v.county + v.addressDetail;
        });

        // 初始化字段
        this.changeData({ key: "list", value: data });
      }
    });
  },
  mounted() {
    // 添加tag标签
    // let list = document.querySelectorAll(".van-address-item");
    // console.log("list==>", list);
    // for (let i = 0; i < list.length; i++) {
    //   let top = list[i].querySelector(".van-address-item__name");
    //   // 在这里遍历生产标签
    //   let tags = this.list[i].tag; //获取标签
    //   console.log("tags==>", tags);
    //   for (let j = 0; j < tags.length; j++) {
    //     let span = document.createElement("span");
    //     span.textContent = tags[j];
    //     span.className =
    //       "van-tag van-tag--round van-tag--primary van-address-item__tag";
    //     top.appendChild(span);
    //   }
    // }
  },
  computed: {
    ...mapState("addressModule", ["list"])
  },
  methods: {
    ...mapMutations("addressModule", ["changeData"]),
    onClickLeft() {
      this.$router.go(-1);
    },
    onAdd() {
      this.$toast("新增地址");
      // 跳转编辑页面
      this.$router.push({ name: "Editaddress" });
    },
    onEdit(item) {
      this.$toast("编辑地址");
      // 跳转编辑页面
      this.$router.push({ name: "Editaddress", params: { aid: item.aid } });
    }
  }
};
</script>

<style lang="scss" scoped>
/deep/ .van-nav-bar {
  .van-icon-arrow-left {
    color: #7f5839;
  }
  .van-nav-bar__text {
    color: #7f5839;
  }
}
.address {
  height: 100%;
}
.tig {
  width: 100%;
  font-size: 22px;
  height: 500px;
  text-align: center;
  line-height: 500px;
}
/deep/ .van-button--danger {
  background-color: #7f5839;
  border: 1px solid #7f5839;
}
/deep/ .van-address-list {
  overflow-y: auto;
  height: calc(100% - 96px);
  padding-bottom: 0;
}

/deep/ .van-radio__icon {
  // opacity: 0;
  display: none;
}
/deep/ .van-address-item {
  border: 1px solid #7f5839;
}
</style> 